











import Vue, { PropType } from "vue";
import { Album } from "@/types/model";
export default Vue.extend({
  name: "AlbumCard",
  props: {
    album: {
      type: Object as PropType<Album>,
      required: true
    }
  },
  data() {
    return {};
  },
  methods: {
    openModal(): void {
      this.$store.state.albumModal = true;
      this.$store.state.selectedAlbum = this.album;
    }
  }
});
