






















import Vue from "vue";
import { baseRoutes } from "@/router/routes";

export default Vue.extend({
  name: "AppNavbar",
  data() {
    return {
      routes: baseRoutes,
      height: 0
    };
  },
  created() {
    this.setHeight();
    addEventListener("resize", this.setHeight);
  },
  destroyed() {
    removeEventListener("resize", this.setHeight);
  },
  methods: {
    setHeight(): void {
      this.height = 0.1 * window.innerHeight;
    }
  }
});
